import { KendoBaseComponent } from '@progress/kendo-base-components-vue-wrapper'
import MediaPlayerProps from './MediaPlayerProps'

import Media from './Media'
import Messages from './Messages'

export default {
    props: MediaPlayerProps,
    mixins: [
        KendoBaseComponent, 
        Media, 
        Messages
    ],
    mounted() {
        kendo.jQuery(this.$el).kendoMediaPlayer(this.widgetOptions)

        this.$emit('kendowidgetready', this.kendoWidget())
    },
    methods: {
        kendoWidget() {
            return kendo.jQuery(this.$el).getKendoMediaPlayer()
        }
    }
}


